	methods: {
		detect_element_in_window: function() {
			let $this = this;
			window.addEventListener('scroll', function() {
				let element = document.querySelector('#target'); // 填入元件css selector
				let position = element.getBoundingClientRect();
				let target_width = element.clientWidth;
				let target_height = element.clientHeight;
				let half_target_height = target_height / 2;

				let partially_judge = position.top < window.innerHeight;
				let fully_judge = position.bottom <= window.innerHeight;
				let custom_judge = partially_judge && (window.innerHeight - position.top > half_target_height);

				if(fully_judge) {
					console.log('該元件可以被完整看到！');
				}

				if(custom_judge) {
					console.log('該元件可看到一半');
				}

				if(partially_judge) {
					$this.partially_judge = true;
					console.log('該元件有一部分可以看到');
				}
			});
		},

		show_video: function(media_srcs) {
			this.media_srcs = media_srcs;
			this.show_video_dialog = true;
			this.media_index = 0;
		},

		ctrl_media_index: function(op) {
			if(op == 'add') {
				if(this.media_index < this.media_srcs.length - 1) {
					this.media_index += 1;
				}
				else {
					this.media_index = 0;
				}
			}
			else if(op == 'minus') {
				if(this.media_index > 0) {
					this.media_index -= 1;
				}
				else {
					this.media_index = this.media_srcs.length - 1;
				}
			}
		}
	}
  }
